<template>
  <div class="model-scene" ref="scene">
    <div class="change-button-container">
      <input
        ref="upload0"
        type="file"
        accept="image/*"
        @change="changeTexture($event, 0)"
      />
      <input
        ref="upload1"
        type="file"
        accept="image/*"
        @change="changeTexture($event, 1)"
      />
      <input
        ref="upload2"
        type="file"
        accept="image/*"
        @change="changeTexture($event, 2)"
      />
      <input
        ref="upload3"
        type="file"
        accept="image/*"
        @change="changeTexture($event, 3)"
      />
    </div>
  </div>
</template>

<script>
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
// import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import {
  CSS2DRenderer,
  CSS2DObject
} from "three/examples/jsm/renderers/CSS2DRenderer";
//   CSS2DObject

export default {
  name: "ModelScene",
  components: {},
  filters: {},
  props: {},
  data() {
    return {
      // scene parameters
      scene: null,
      camera: null,
      renderer: null,
      controls: null,
      glTFLoader: null,
      loader: null,
      // rotation path
      curve: null,
      labelCurve: null,
      t: 0,
      tList: [0, 0.25, 0.5, 0.75],
      // mesh group
      cubeList: [],
      curveList: [],
      // cube selector
      currentCubeId: -1,
      // rotation
      center: null
    };
  },
  computed: {},
  watch: {},
  beforeCreate() {},
  created() {},
  beforeMount() {},
  mounted() {
    // handle browser resize
    window.addEventListener("resize", this.handleOnResize, false);
    // setup renderer
    this.renderer = new THREE.WebGLRenderer();
    this.renderer.setSize(window.innerWidth, window.innerHeight);
    this.renderer.antialias = true;
    this.renderer.setPixelRatio(window.devicePixelRatio);
    this.$refs.scene.appendChild(this.renderer.domElement);
    this.labelRenderer = new CSS2DRenderer();
    this.labelRenderer.setSize(window.innerWidth, window.innerHeight);
    this.labelRenderer.domElement.style.position = "absolute";
    this.labelRenderer.domElement.style.top = "0px";
    this.$refs.scene.appendChild(this.labelRenderer.domElement);
    // scene setup
    this.scene = new THREE.Scene();
    this.scene.background = new THREE.Color("#333333");
    this.scene.fog = new THREE.FogExp2("#333333", 0.25);
    // camera setup
    this.camera = new THREE.PerspectiveCamera(
      75,
      window.innerWidth / window.innerHeight,
      0.1,
      1000
    );
    this.camera.lookAt(0, 0, 0);
    this.camera.position.set(0, 0, 2);
    this.scene.add(this.camera);
    // control setup
    this.controls = new OrbitControls(
      this.camera,
      this.labelRenderer.domElement
    );
    // path setup
    this.curve = new THREE.CatmullRomCurve3([
      new THREE.Vector3(0, 0.2, 0),
      new THREE.Vector3(0.2, 0, 0),
      new THREE.Vector3(0, -0.2, 0),
      new THREE.Vector3(-0.2, 0, 0)
    ]);
    this.curve.curveType = "centripetal";
    this.curve.closed = true;
    // cube label curve setup
    this.labelCurve = new THREE.CatmullRomCurve3([
      new THREE.Vector3(0.1, 0.1, 0),
      new THREE.Vector3(0.1, -0.1, 0),
      new THREE.Vector3(-0.1, -0.1, 0),
      new THREE.Vector3(-0.1, 0.1, 0)
    ]);
    this.labelCurve.curveType = "centripetal";
    this.labelCurve.closed = true;
    // // load models
    // this.glTFLoader = new GLTFLoader();
    // this.glTFLoader.load("/models/scene/scene.gltf", gltf => {
    //   let mesh = gltf.scene;
    //   this.scene.add(mesh);
    //   mesh.position.set(0, 0, 2);
    //   this.animate();
    // });
    // rotation center
    this.center = new THREE.Object3D();
    this.scene.add(this.center);
    // cube setup
    const geometry = new THREE.BoxGeometry(0.1, 0.1, 0.1);
    this.loader = new THREE.TextureLoader();
    this.loader.load("/textures/wall.jpeg", texture => {
      let material = new THREE.MeshBasicMaterial({
        map: texture
      });
      // wire setup
      for (let i = 0; i < this.tList.length; i += 1) {
        let t1 = 0;
        let t2 = 0;
        if (i === this.tList.length - 1) {
          t1 = this.tList[i];
          t2 = this.tList[0];
        } else {
          t1 = this.tList[i];
          t2 = this.tList[i + 1];
        }
        let p1 = this.curve.getPoint(t1);
        let p2 = this.curve.getPoint(t2);
        let pc = new THREE.Vector3(p1.x + p2.x, p1.y + p2.y, 0);
        const curve = new THREE.QuadraticBezierCurve(p1, pc, p2);
        const points = curve.getPoints(50);
        const geometryCurve = new THREE.BufferGeometry().setFromPoints(points);
        const material = new THREE.LineBasicMaterial({
          color: "#222"
        });
        const curveObject = new THREE.Line(geometryCurve, material);
        this.center.add(curveObject);
      }
      this.tList.forEach((t, index) => {
        let cube = new THREE.Mesh(geometry, material);
        // label setup
        let cubeDiv = document.createElement("div");
        cubeDiv.className = "label";
        cubeDiv.textContent = "upload material";
        cubeDiv.addEventListener("click", () => {
          this.clickLabel(index);
        });
        let cubeLabel = new CSS2DObject(cubeDiv);
        cube.add(cubeLabel);
        let labelPos = this.labelCurve.getPoint(t);
        cubeLabel.position.set(labelPos.x, labelPos.y, 0);
        this.center.add(cube);
        let pos = this.curve.getPoint(t);
        cube.position.set(pos.x, pos.y, 0);
        this.cubeList.push(cube);
      });
    });
    this.animate();
  },
  beforeUpdate() {},
  updated() {},
  activated() {},
  deactivated() {},
  beforeDestroy() {},
  destroyed() {},
  methods: {
    animate() {
      requestAnimationFrame(this.animate);
      this.renderer.render(this.scene, this.camera);
      this.labelRenderer.render(this.scene, this.camera);
      // this.center.rotation.z += 0.1;
    },
    handleOnResize() {
      this.camera.aspect = window.innerWidth / window.innerHeight;
      this.camera.updateProjectionMatrix();
      this.renderer.setSize(window.innerWidth, window.innerHeight);
      this.labelRenderer.setSize(window.innerWidth, window.innerHeight);
    },
    changeTexture(e, id) {
      const imageURL = URL.createObjectURL(e.target.files[0]);
      this.loader.load(imageURL, texture => {
        this.cubeList[id].material = new THREE.MeshBasicMaterial({
          map: texture
        });
      });
      this.currentCubeId = -1;
    },
    clickLabel(id) {
      this.currentCubeId = id;
      this.$refs[`upload${id}`].click();
    }
  }
};
</script>

<style scoped>
.model-scene {
  /*width: 100vw;*/
  /*height: 100vh;*/
}
.change-button-container {
  width: 0;
  height: 0;
  visibility: hidden;
}
</style>
